#Transport2 .Team_page_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

#Transport2 .team_page_containt {
    width: 90%;
}

#Transport2 .team_page_containt  h2 {
    font-size: clamp(1.4rem, 2.2vw, 3vw);
    color: rgb(43, 43, 43);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: .5rem;
    padding: 0 5vw;
}

#Transport2 .team_page_containt .description {
  font-size: 1.1rem;
  font-size: clamp(.96rem, 1.2vw, 2rem);
  line-height: 1.6rem;
  color: rgb(89, 89, 89);
  padding: 0 5vw;

}

#Transport2 .team_page_containt .team_list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(20rem, 28%));
    grid-template-columns: repeat(auto-fill, minmax(20rem, 25%));
    justify-content: center;
    gap: 4rem;
    row-gap: 5rem;
    margin-top: 4rem;
}

#Transport2 .team_page_containt .team_list .single_team {
    border-bottom: 1px solid rgb(194, 194, 194);
}

#Transport2 .team_page_containt .single_team .image_box {
    width: 100%;
    height: 15rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.149);
    overflow: hidden;
    background-color: white;
}

#Transport2 .team_page_containt .team_list .single_team img {
    width: 93%;
    height: 93%;
    object-fit: cover;
    transition: .5s;

    
}

#Transport2 .team_page_containt .single_team:hover .image_box img {
    scale: 1.021;

}

#Transport2 .team_page_containt .single_team .details_box {
    position: relative;
    width: 100%;
    overflow: hidden;
}

#Transport2 .team_page_containt .team_list .single_team h3 {
    font-size: 1.4rem;
    width: 100%;
    text-align: center;
    color: rgb(30, 30, 30);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: .7rem 0;
    margin-bottom: .0rem;
}

#Transport2 .team_page_containt .single_team .job {
    font-size: 1.1rem;
    color: #ff4d00d6;
    text-align: center;
    margin: .0rem 0;
}

#Transport2 .team_page_containt .single_team p {
    font-size: 1.041rem;
    line-height: 1.6rem;
    color: rgb(61, 61, 61);
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#Transport2 .team_page_containt .p {
    margin-top: .5rem;
    font-size: .91rem !important;
    line-height: 1.5rem !important;
    color: red;
    padding: 0 2rem;
    margin-bottom: 1rem;
}


#Transport2 .team_page_containt .single_team .info {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: #1C2755 !important;
    color: white;
    transition: .5s;
    top: 18rem;
    bottom: 0;
    overflow: hidden;
}

#Transport2 .team_page_containt .single_team:hover .info {
    /* transform: translateY(0); */
    top: 5rem;
}

#Transport2 .team_page_containt .single_team .info .media {
    display: flex;
    align-items: center;
    gap: 2rem;
    font-size: 1.5rem;
    color: #f8a555;
}

#Transport2 .team_page_containt .single_team .media i {
    cursor: pointer;
}

#Transport2 .team_page_containt .single_team .info p {
    font-size: 1.2rem;
    margin: 0;
    margin-top: .5rem;
    color: rgba(255, 255, 255, 0.752);
}

@media (max-width: 501px) {
    #Transport2 .team_page_containt {
        width: 90%;
        transform: translateX(-.5rem);
    }



    #Transport2 .team_page_containt .team_list {
    grid-template-columns: repeat(auto-fit, 80vw) !important;
    grid-template-columns: 1fr;
    }

    #Transport2 .team_page_containt .single_team .info .media {
        gap: 1.2rem;
        font-size: 1.1rem;
    }

    #Transport2 .team_page_containt .single_team .info p {
        font-size: 1rem;
    }


}