#Pricing1 .Pricing_section_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

#Pricing1 .pricing_section_containt {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    width: 80%;
    gap: 2rem;
    margin-top: 5rem;
}

#Pricing1 .pricing_section_containt .single_price {
    text-align: center;
    box-shadow: 0 0 1rem rgba(128, 128, 128, 0.322);
 }

#Pricing1 .pricing_section_containt .single_price h3 {
    font-size: 1.25rem;
    margin: 0;
    padding: .5rem;
    padding-top: 1.5rem;
    background-color: white;
    color: #1C2755;
}

#Pricing1 .pricing_section_containt .single_price h3.center {
    color: #14a2bc;
}

#Pricing1 .pricing_section_containt .single_price .price_section {
    display: flex;
    justify-content: center;
    align-items: end;
    font-size: 5rem;
    width: 100%;
    background-color: white;
    color: #1C2755;
    
}

#Pricing1 .pricing_section_containt .single_price .price_section.center {
    color: #14a2bc;
}

#Pricing1 .pricing_section_containt .single_price .price_section p {
    margin: 0;
}



#Pricing1 .pricing_section_containt .single_price .price_section  .dollar {
    display: inline;
    font-weight: bold;
    font-size: 1.3rem;
    transform: translateY(-2.5rem);
    margin-right: .2rem;
    color: gray;
}

#Pricing1 .pricing_section_containt .single_price .price_section .unite {
    font-weight: bold;
    font-size: 1rem !important;
    transform: translateY(-.8rem);
    margin-left: .2rem;

}


#Pricing1 .pricing_section_containt .single_price .details {
    margin: 0;
    padding: 1rem 0;
    color: rgb(46, 46, 46);
    background-color: white;
    
}

#Pricing1 .pricing_section_containt .single_price .gray {
    background-color: rgba(43, 90, 167, 0.105);
}


#Pricing1 .pricing_section_containt .single_price button {
    cursor: pointer;
    font-size: 1.1rem;
    width: 100%;
    padding: 1.5rem 0;
    border: none;
    background-color: #1C2755;
    color: white;

}

#Pricing1 .pricing_section_containt .single_price button.center {
    background-color: #f8a555;
}











@media (max-width: 1100px) {
    #Pricing1 .pricing_section_containt {
        grid-template-columns: 1fr 1fr;
        row-gap: 9rem;
    }
}


@media (max-width: 700px) {
    #Pricing1 .pricing_section_containt {
        grid-template-columns: 1fr;
        row-gap: 6rem;
    }
}


@media (max-width: 501px) {
    #Pricing1 .pricing_section_containt .single_price .details {
        padding: .6rem 0;        
    }

    #Pricing1 .pricing_section_containt .single_price button {
        padding: .81rem 0;
    }

    #Pricing1 .pricing_section_containt .single_price .price_section {
        font-size: 5rem;       
    }

    #Pricing1 .pricing_section_containt .single_price .price {
        font-size: 20vw;
    }

}

