#Team1 .Team_container {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100vw;
    margin-bottom: 8rem;
}

#Team1 .team_containt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
}

#Team1 .team_containt h2 {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
     font-size: 2rem;
     margin-bottom: 3rem;
     color: rgba(0, 0, 0, 0.866);
     
}

#Team1 .team_containt .member_list {
    display: flex; 
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    width: 100%;
}

#Team1 .team_containt .member_list .single_member {
    display: flex;
    flex-direction: column;
    width: 25%;
    min-width: 15rem;
    transition: .25s;
}

#Team1 .team_containt .member_list .single_member:hover {
    transform: translateY(-.51rem);
}


#Team1 .team_containt .single_member .top {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: end;
    height: 17rem;
    transition: .25s;
}

#Team1 .team_containt .single_member:hover .top {
    background-color: rgba(0, 0, 0, 0.556);
}

#Team1 .team_containt .single_member .media {
    display: flex;
    gap: 1.5rem;
    font-size: 1.3rem;
    padding-bottom: 2rem;
    opacity: 0;
    transition: .25s;
    color: white;
}

#Team1 .team_containt .single_member:hover .media {
    opacity: 1;
}

#Team1 .team_containt .media i:hover {
    cursor: pointer;
    color: #f8a555;
}

#Team1 .team_containt .single_member img {
    position: absolute;
    object-fit: cover;
    object-position: top;
    width: 100%;
    height: 100%;
    z-index: -1;
}


#Team1 .team_containt .single_member .buttom {
    background-color: #1C2755;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: .25s;
    color: white;
}

#Team1 .team_containt .single_member:hover .buttom {
    background-color: #f8a555;
}

#Team1 .team_containt .single_member .buttom h3 {
    margin: 1rem 0;
    margin-bottom: .5rem;
}

#Team1 .team_containt .single_member .buttom p {
    margin-top: 0;
    margin-bottom: 1.3rem;
    color: rgba(255, 255, 255, 0.786);
}


@media (max-width: 1130px) {
    #Team1 .team_containt .member_list {
        display: grid;
        grid-template-columns: repeat(auto-fill, 20rem);
    }
    #Team1 .team_containt .member_list .single_member {
        width: 20rem;
    }
}


@media  (max-width: 501px) {
    #Team1 .team_containt .member_list {
        row-gap: 3rem;
    }


    #Team1 .team_containt .member_list {
        grid-template-columns: 1fr;
    }  

    #Team1 .team_containt .member_list .single_member {
        width: 100%;
    }

    #Team1 .team_containt h2 {
        font-size: clamp(1.4rem , 7vw, 2rem);
        margin-bottom: 2rem;
    }
}

