.comment_blog_containt .leave_comment {
    display: flex;
    border: 1px solid gray;
    width: 100%;
    border-radius: 10rem;
    overflow: hidden;
    margin-bottom: 3rem;
}

.comment_blog_containt .leave_comment input {
    width: 85%;
    height: 2.5rem;
    font-size: 1.2rem;
    border: none;
    margin-left: 2rem;
}

.comment_blog_containt .leave_comment input:focus {
    outline: none;
}

.comment_blog_containt .leave_comment button {
    width: 15%;
    min-width: 5rem;
    border: none;
    height: 2.7rem;
    background-color: #f8a555;
    color: white;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
}


.comment_blog_containt .single_comment {
    border: 1px solid rgba(0, 0, 0, 0.126);
    border-bottom: none;
    display: flex;
    gap: 1rem;
    padding: 1.5rem 1rem;
}

.comment_blog_containt .title {
    font-size: clamp(1.5rem , 2vw, 3vw);
    margin-bottom: 2.5rem;
    margin-top: 4rem;
}


.comment_blog_containt .single_comment img {
    width: 4rem;
    height: 4rem;
    border-radius: 10rem;
    object-fit: cover;
}

.comment_blog_containt .single_comment .name {
    margin: 0;
    font-size: 1.2rem;
    font-weight: 600;
}

.comment_blog_containt .single_comment .comment {
    font-size: 1.04rem;
    color: rgba(0, 0, 0, 0.735);
    margin:  .5rem 0;
    line-height: 1.7rem;
    font-weight: 400;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.comment_blog_containt .single_comment .date_section {
    display: flex;
    align-items: center;
    gap: 2rem;
    color: #f8a555;
}

.comment_blog_containt .single_comment .date_section button {
    background-color: black;
    border: none;
    color: white;
    padding: .3rem .6rem;
    border-radius: .2rem;
    cursor: pointer;
}