#Testimonial1 .Review_container {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100%;
    padding-top: 5rem;
    background-color: #f8a555;
}

#Testimonial1 .review_containt {
    display: flex;
    width: 90%;
    gap: 1.5rem;
}

#Testimonial1 .review_containt .left_section {
    width: 40%;
}

#Testimonial1 .review_containt .left_section h2 {
    text-transform: uppercase;
    margin: 0;
    font-size: 1.82rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(41, 41, 41) ;
}

#Testimonial1 .review_containt .left_section img {
    object-position: top;
    object-fit: cover;
    margin-top: 3rem;
    width: 30vw;
    min-width: 24rem;
    transform: translateY(.4rem);
}





/* /////////////////////////////////////////////////////////////// */
#Testimonial1 .review_containt .right_section {
    width: 60%;
}

#Testimonial1 .review_containt .slider_box {
    display: flex;
    overflow: hidden;
    width: 100%;
    transition: .5s;
    

}

#Testimonial1 .review_containt .slider_box .single_slider {
    min-width: 100%;
    transform: translateX(-00%);
    transition: .5s;

}

#Testimonial1 .review_containt .right_section .comments {
    position: relative;
    padding: 2.3rem 2rem ;
    z-index: 2;
    border-radius: 1rem;
    background-color: white;
}

#Testimonial1 .review_containt .right_section .comments p {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3; 
    overflow: hidden;
    text-overflow: ellipsis;
    z-index: 2;
    font-size: clamp(1rem, 1.2vw, 3vw);
    margin: 0;
    line-height: 1.8rem;
    color: rgba(21, 21, 21, 0.847);
}

#Testimonial1 .review_containt .right_section .comments i {
    position: absolute;
    font-size: 2.2rem;
    top: .3rem;
    left: .3rem;
    color: #f8a555;
}

#Testimonial1 .review_containt .right_section .profil {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
}

#Testimonial1 .review_containt .profil .user {
    display: flex;
    align-items: center;
    gap: 1rem;
}

#Testimonial1 .review_containt .profil .user img {
    object-fit: cover;
    width: 5rem;
    height: 5rem;
    border-radius: 10rem;
    background-color: rgba(255, 255, 255, 0.892);
}

#Testimonial1 .review_containt .profil .user h5 {
    margin: 0;
    font-size: 1.2rem;
    color: rgb(50, 50, 50);
}

#Testimonial1 .review_containt .profil .user p {
    text-transform: uppercase;
    margin: .5rem 0; 
    color: white;
}

#Testimonial1 .right_section .profil .dots {
    display: flex;
    align-items: start;
}

#Testimonial1 .review_containt .profil .dots i {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 3.3rem;
    width: 2rem;
    height: 1.8rem;
    color: white;

}





#Testimonial1 .arrow-right {
    position: absolute;
    z-index: -1;
    width: 0; 
    height: 0; 
    border-top: 60px solid transparent;
    border-bottom: 60px solid transparent;
    border-left: 60px solid white;
    bottom: -2rem;
    left: 4.5rem;
  }

  /* services_section */
  #Testimonial1 .review_containt .services_section {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 4rem;
    margin-bottom: 2rem;
    gap: 1rem;
    color: white;
  }

  #Testimonial1 .review_containt .services_section .service1 i{
      font-size: 3.2rem;
  }

  #Testimonial1 .review_containt .services_section .service1 h4 {
    margin: .5rem 0;
    font-size: 1.4rem;
  }

  #Testimonial1 .review_containt .services_section .service1 p {
    font-size: 1.3rem;
    line-height: 1.5rem;
    font-size: clamp(1rem, 1.2vw, 3vw);
    color: rgb(255, 255, 255);
  }





  @media (max-width: 1050px) {
    #Testimonial1 .review_containt .left_section {
        display: none;
    }

    #Testimonial1 .review_containt .right_section {
        width: 100%;
    }

    #Testimonial1 .review_containt .right_section h2 {
        margin: 0;
        margin-bottom: 2.5rem;
        font-size: clamp(1.4rem, 4vw, 3rem);
        color: rgb(31, 31, 31);

    }
  }

  @media (max-width: 800px) {
    #Testimonial1 .Review_container {
        padding-top: 3rem;
    }


    #Testimonial1 .review_containt .services_section {
        flex-direction: column;
        gap: 1rem;
    }

    #Testimonial1 .review_containt .services_section .service1 {
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        padding: 1rem;
        border: 1px solid rgba(255, 255, 255, 0.43);
    }


  }

  @media (max-width: 501px) {
    #Testimonial1 .review_containt .profil .user img {
        width: 3rem;
        height: 3rem;
    }

    #Testimonial1 .review_containt .profil .user h5 {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        font-size: 1.1rem;
        width: 50vw;
    }



    #Testimonial1 .review_containt .profil .user p {
        font-size:  .71rem;
        margin-top: .2rem;
    }
    #Testimonial1 .review_containt .profil .dots i {
        font-size: 3rem;
        width: 1.52rem;
        
    }
  }


  @media (max-width: 400px) {
    #Testimonial1 .review_containt .right_section .profil {
        display: flex;
        flex-direction: column;
    }

    #Testimonial1 .review_containt .right_section .dots {
        display: flex;
        justify-content: end;
        width: 100%;
    }


     #Testimonial1 .arrow-right {
        bottom: -1.5rem;
        left: 2.8rem;
    }
  }