#About1 .About_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 9rem;
    padding-bottom: 5rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.332);
}

#About1 .about_containt {
    display: flex;
    justify-content: center;
    /* align-items: center; */
    width: 90%;
    /* border: 1px solid red; */
    gap: 2rem;

}

/* Inof section */

#About1 .about_containt .info_section {
    width: 100%;
    /* border: 1px solid red; */
}

#About1 .about_containt .info_section h2 {
    font-size: 2.3rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin: 0;
}

#About1 .about_containt .info_section p {
    font-size: 1.2rem;
    color: rgb(95, 95, 95);
    line-height: 1.88rem;
}

#About1 .about_containt .p_img {
    display: inline-block;
    border-bottom: 1px solid rgba(128, 128, 128, 0.271);
    padding-bottom: 1.5rem;
}

#About1 .about_containt .info_section img {
    width: 20vw;
    float: left;
    margin: 0 1rem 0rem 0;
}


/* avalible */
#About1 .about_containt .info_section .avalible {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
}

#About1 .about_containt  .avalible .left,
#About1 .about_containt  .avalible .right {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    /* gap: 1rem; */
}

#About1 .about_containt .avalible .left div, 
#About1 .about_containt .avalible .right div {
    display: flex;
    gap: .9rem;
    align-items: center;

} 

#About1 .about_containt .avalible p {
    margin: .3rem 0;
    color: black;
    font-weight: 500;
}

#About1 .about_containt .avalible i {
    font-size: 1.4rem;
}

#About1 .about_containt .info_section .avalible .left {
    width: 50%;
    /* border: 1px solid red; */
}


#About1 .about_containt .info_section .avalible .right {
   width: 50%;
   /* border: 1px solid red; */
} 







/* Form section */

#About1 .about_containt .form_section {
    width: 35rem;
    /* border: 1px solid black; */
}

#About1 .about_containt .form_section .form_header {
    display: flex;
    align-items: center;
    border-bottom: 1px solid gray;
    color: white;
    background-color: black;
    height: 8rem;
    gap: 1rem;
}

#About1 .about_containt .form_section .form_header img {
    width: 11rem;
    height: 11.5rem;
    /* transform: translateY(-3.5rem); */
    /* transform: translateX(1rem); */
    transform: translate(-1rem, -1.8rem);
    object-fit: cover;
    object-position: top;
    /* border: 1px solid red; */
}

#About1 .about_containt .form_header h3 {
    scale: 1.6;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;


}

#About1 .about_containt .form_section form {
    padding: 2rem 1rem;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    gap: .8rem;
    background-color: black;
}

#About1 .about_containt .form_section input {
    width: 97.9%;
    font-size: 1rem;
    padding: .4rem 0rem;
    background-color: white;
    border: 1px solid black;
}

#About1 .about_containt .form_section input::placeholder {
    padding-left: 1rem;
}

#About1 .about_containt .from_to {
    display: flex;
    gap: .5rem;
}

#About1 .about_containt .form_section textarea::placeholder {
    padding-left: 1rem;
}

#About1 .about_containt .form_section textarea {
    width: 97.9%;
    height: 6rem;
    font-size: 1.2rem;

}

#About1 .about_containt .form_section button {
    width: 100%;
    margin: 0;
    height: 2.3rem;
    margin-top: 1rem;
}


@media (max-width: 900px) {
    #About1 .about_containt  {
        flex-direction: column;
        gap: 8rem;
    }

    #About1 .about_containt .form_section {
        width: 100%;
    }

    .about_containt .form_section input,
    #About1 .about_containt .form_section textarea {
        width: 99.5%;
    }

    #About1 .about_containt .form_header  h3 {
        text-align: center;
        width: 60%;
        
    }

    #About1 .about_containt .info_section img {
        width: 45vw;

    }

    #About1 .about_containt .info_section h2 {
        font-size: clamp(1.7rem, 5vw, 4rem);
        
    
    }


}


@media (max-width: 600px) {
    #About1 .about_containt .form_header  h3 {
        width: auto;
    }
}

@media (max-width: 501px) {
    #About1 .about_containt .info_section img {
        width: 100%;
        float: none;
        margin: 0 1rem 0rem 0;
    }

    #About1 .about_containt .avalible {
        flex-direction: column;
        /* gap: 0rem !important; */
    }

    .about_containt .avalible .left,
    #About1 .about_containt .avalible .right {
        width: 100% !important;

    }

    #About1 .about_containt .form_header h3 {
        font-size: clamp(.341rem, 4vw, 2rem);
    }

    #About1 .about_containt .form_section {
        transform: translateX(-.51rem);
    }
}