#Contact1 .contact_section {
    display: flex;
    justify-content: center;
    width: 100vw;
}

#Contact1 .contact_section_containt {
    width: 80%;
}

#Contact1 .contact_section_containt .title_section h2 {
    font-size: clamp(1.4rem, 2.1vw, 3vw);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 0;
}

#Contact1 .contact_section_containt .title_section p  {
    font-size: clamp(1rem, 1.18vw, 4vw);
    line-height: 1.8rem;
    color: rgb(83, 83, 83);
}

#Contact1 .contact_section_containt .form_container {
    display: flex;
    margin-top: 5rem;
    gap: 2rem;
}

#Contact1 .contact_section_containt .info_section {
    display: flex;
    flex-direction: column;
    width: 30%;
    min-width: 20rem;
}

#Contact1  .single_contact_info {
    display: flex;
    align-items: center;
    width: auto !important;
    gap: 1rem;
    border: 1px solid rgb(193, 193, 193);
    padding: 1.2rem .9rem;
}

#Contact1  .single_contact_info.email {
    border-top: none;
    border-bottom: none;
}

#Contact1  .single_contact_info i {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 3.5rem;
    min-height: 3.5rem;
    border-radius: 10rem;
    font-size: 1.5rem;
    border: 1px solid black;
}

#Contact1  .single_contact_info p {
    font-size: clamp(1rem, 1.18vw, 4vw);
    color: rgb(67, 67, 67);
}


#Contact1 .contact_section form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 70%;
}

#Contact1 .contact_section form input {
    height: 3rem;
    font-size: 1.152rem;
    padding-left: 1rem;
    border: 1px solid rgb(212, 212, 212);
    background-color: rgba(227, 227, 227, 0.626);

}

#Contact1 .contact_section form input:focus {
    outline: 1px solid rgb(183, 183, 183);
}

#Contact1 .contact_section form textarea {
    height: 10rem;
    font-size: 1.152rem;
    padding-left: 1rem;
    border: 1px solid rgb(212, 212, 212);
    background-color: rgba(227, 227, 227, 0.626);


}

#Contact1 .contact_section form textarea:focus {
    outline: 1px solid rgb(183, 183, 183);

}

#Contact1 .contact_section form button {
    cursor: pointer;
    width: 10rem;
    height: 3rem;
    font-size: 1.15rem;
    transition: .325s;
    border: 1px solid rgb(183, 183, 183);
}

#Contact1 .contact_section form button:hover {
    width: 12rem;
}

@media (max-width: 900px) {
    #Contact1 .contact_section_containt .form_container {
        flex-direction: column;
    }

    #Contact1 .contact_section_containt .info_section {
        width: 100%;
    }

    #Contact1 .contact_section_containt form {
        width: 100%;
    }
}

@media (max-width: 501px) {
    #Contact1 .contact_section_containt {
        width: 90%;
    }
}