.Mission_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 8rem;
    padding:  6rem 0;
    background-color: rgba(128, 128, 128, 0.201);
    /* transform: translateX(-1rem); */
}

.mission_containt {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
}

/* .mission_containt .mission, */
 .mission_containt .details {
    width: 100%;
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
    gap: 1rem;
}

.mission_containt h3 {
    margin-top: 0;
    font-size: 1.62rem;
    /* border-bottom: 2px solid orange; */
    /* color: orangered; */
}

.mission_containt p {
    width: 60%;
    font-size: 1.1rem;
    color: rgba(0, 0, 0, 0.755);
    margin: 0;
    line-height: 1.7rem;
}

.mission_containt img {
    width: 40%;
    border: .5rem solid white;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.25);
    object-fit: cover;
}

@media (max-width: 1100px) {
    .mission_containt {
        width: 90%;
    }
}

@media (max-width: 950px) {
    .mission_containt {
        flex-direction: column;
    }
}

@media (max-width: 501px) {
    .mission_containt .details {
        flex-direction: column !important;
        gap: 2rem;
    }

    .mission_containt p {
        width: 90%;
    }

    .mission_containt img {
        width: 60vw;
    }
}




