.Faq_page_containter {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5rem 0;
    margin-top: 5rem;
}

.Faq_page_containter h2{
    width: 80%;
    font-size: 2.2rem;
    margin-bottom: 4rem;
}

.Faq_page_containter .faq_containt {
    width: 80%;
    display: flex;
    gap: 2rem;
    justify-content: center;
    margin-bottom: 7rem;

}

.Faq_page_containter .faq_containt img {
    width: 45%;
    height: 22rem;
    object-fit: cover;
}

.Faq_page_containter .faq_containt .questions_list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    /* width: 55%; */
    width: 100%;
}

.Faq_page_containter .faq_containt .single_question {
    width: 100%;
    overflow: hidden;
    transition: .25s;
    border: 1px solid rgba(128, 128, 128, 0.27);
    background-color: white;
}

.Faq_page_containter .faq_containt .single_question.active .answer {
    height: auto;
    height: 7rem;
    position: relative;
}

.Faq_page_containter .faq_containt .single_question .answer.active {
    /* height: auto;
    height: 100%; */
    height: 14rem;
    height: 8rem;
}

.Faq_page_containter .faq_containt .single_question.active .question {
    border-bottom: 1px solid rgba(128, 128, 128, 0.539);
    border-bottom: 1px solid rgba(128, 128, 128, 0.27);


}


.Faq_page_containter .faq_containt .question {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: .71rem;
    padding: 0 .6rem;
    padding-right: 2rem;
    background-color: rgba(128, 128, 128, 0.242);
}


.Faq_page_containter .faq_containt .question i {
    cursor: pointer;
    font-size: 1.52rem;
}

.Faq_page_containter .faq_containt .question i:hover {
   color: #f8a555;

}

.Faq_page_containter .faq_containt h3 {
    font-size: 1.2rem;
    padding: .6rem 0rem;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(40, 40, 40);
}


.Faq_page_containter .faq_containt p {
    font-size: 1.2rem;
    color: rgb(48, 48, 48);
    margin: 1rem .6rem;
    line-height: 1.72rem;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
}

.Faq_page_containter .faq_containt .answer.active p {
    -webkit-line-clamp: 200; 
    /* transition: .25s; */
   
}

.Faq_page_containter .faq_containt .answer {
    display: flex;
    /* align-items: end; */
    height: 0;
    overflow: hidden;
    transition: .5s;

}

.Faq_page_containter .faq_containt .answer p span {
    color: rgba(0, 106, 255, 0.792);
    cursor: pointer;
    text-decoration: underline;
}

.Faq_page_containter .faq_containt .single_question .Read_more {
    position: absolute;
    bottom: 1.1rem;
    right: .51rem;
    color: rgba(0, 106, 255, 0.792);
    text-decoration: underline;
    cursor: pointer;
    /* line-height: 3.82rem; */
    background-color: white;
    margin: 0;
    /* transform: translateX(-1rem); */
}


@media  (max-width: 1100px) {
    .Faq_page_containter h2 {
        width: 90%;
    }


    .Faq_page_containter .faq_containt {
        width: 90%; 
    }
}

@media (max-width: 1000px) {
    .Faq_page_containter .faq_containt img {
        display: none;
    }

    .Faq_page_containter {
        padding: 0 !important;
        margin: 0;
        width: 100vw;
        transform: translateX(-.71rem);
    }

    .Faq_page_containter .faq_containt .questions_list {
        width: 100%;
    }

    .Faq_page_containter h2 {
        margin-bottom: 3rem;
        font-size: clamp(1rem, 7vw, 2.5rem) !important;
    }
}




@media (max-width: 501px) {
    .Faq_page_containter .faq_containt .single_question .answer.active {
        height: 20rem;
        height: 100%;
        height: auto;
    }
}



