#Action1 .Action_container {
    width: 100vw;
    margin-bottom: 8rem;

}

#Action1 .action_containt {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 25rem;
    color: white;
    background-color: rgba(27, 32, 42, 0.838);
}

#Action1 .background {
    position: absolute;
    object-fit: cover;
    object-position: bottom;
    z-index: -1;
    width: 100%;
    height: 100%;
}

#Action1 .action_containt h4 {
    text-transform: uppercase;
    text-align: center;
    font-size: 1.7rem;
    margin: 0.8rem;
    width: 80%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: white;
}



#Action1 .action_containt p {
    text-align: center;
    width: 85%;
    font-size: 1.4rem;
    max-width: 36rem;
    color: rgba(255, 255, 255, 0.773);
}

#Action1 .action_containt .phone {
    color: #f8a555;
}

/* ////////////////////////////////////////////////////////////////////////////// */


#Action1 .Action_container .buttom {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 12rem;
    color: white;
    background-color: #1C2755;
}



#Action1 .Action_container .buttom .img_box {
    position: relative;
    width: 33.33%;
}

#Action1 .Action_container .buttom .img_box img {
    position: absolute;
    object-position: top;
    object-fit: cover;
    bottom: 0rem;
    right: 4rem;
    width: 20rem;
    height: 21rem;
}

@font-face {
    font-family: playball;
    src: url(./font-family/Playball-Regular.ttf);
}

#Action1 .Action_container .mobile_section {
    display: flex;
    align-items: center;
    gap: 2rem;
    width: 33.33%;
}

#Action1 .Action_container .mobile_section .left {
    position: relative;
}

#Action1 .Action_container .mobile_section .left img {
    position: absolute;
    left: -2.3rem;
    bottom: -.1rem;
}

#Action1 .Action_container .mobile_section .left p {
    text-align: center;
    font-family: playball;
    font-size: 2rem;
    margin: 0;
}

#Action1 .Action_container .mobile_section .right p {
    white-space: nowrap;
    font-size: 1.2rem;
    margin: .3rem;
    color: rgba(255, 255, 255, 0.892);
}


#Action1 .Action_container .app_section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 33.33%;
    gap: 1rem;
}

#Action1 .Action_container .app_section .app {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: .4rem;
    padding: .4rem .5rem;
    border-radius: .5rem;
}

#Action1 .Action_container .apple {
    background-color: rgb(0, 179, 255);
}


#Action1 .Action_container .android {
    background-color: yellowgreen;
}

#Action1 .Action_container .app i {
    font-size: 2.23rem;
}



#Action1 .Action_container .app .location {
    margin: 0;
    font-size: .8rem;
}

#Action1 .Action_container .app .store {
    margin: 0;
    font-size: 1.4rem;
    line-height: 1.21rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}


@media (max-width: 1200px) {
    #Action1 .Action_container .right {
        display: none;
    }

    #Action1 .Action_container .buttom {
        justify-content: start !important;
        
    }

    #Action1 .Action_container .buttom .img_box  {
        min-width: 25rem;
    }

    #Action1 .Action_container .mobile_section {
        min-width: 10rem;
        width: 20%;
    }
}

@media (max-width: 980px) {
    #Action1 .Action_container .buttom .mobile_section {
        display: none;
    }

    #Action1 .Action_container .app_section {
        width: 100%;
    }

    #Action1 .Action_container .buttom {
        height: 10rem;
    }

    #Action1 .Action_container .buttom .img_box img {
        object-position: top;
        object-fit: cover;
        bottom: 0rem;
        right: 0rem;
        width: 15rem;
        height: 16rem;
    }

    #Action1 .Action_container .buttom .img_box  {
        min-width: 20rem;
    }
    
}

@media (max-width: 800px) {
    #Action1 .Action_container .buttom .img_box {
        display: none ;
    }
}

@media (max-width: 501px) {
    #Action1 .Action_container .buttom .app_section .app {
        max-width: 34vw;
    }

    #Action1 .action_containt {
        height: 32rem;
    }

    #Action1 .Action_container .buttom .app .location {
        font-size: 2.5vw;
    }

    #Action1 .Action_container .buttom .store {
        font-size: 5vw;
    }

    #Action1 .Action_container {
        transform: translateX(-.5rem);
    }

    #Action1 .action_containt h4 {
        font-size: clamp(.4rem, 7vw, 1.72rem);
    }
}

