.History_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 2rem 0; */
    transform: translateX(-1rem);
}

.history_containt {
    display: flex;
    width: 80%;
    gap: 2rem;
}

.history_containt .description {
     width: 60%;
} 

.history_containt h3 {
    font-size: 2rem;
    margin-top: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}


.history_containt p {
    font-size: 1.1rem;
    line-height: 1.8rem;
    color: rgba(0, 0, 0, 0.712);
}

.history_containt img {
    width: 40%;
    min-width: 25rem;
    object-fit: cover;
    border: .5rem solid white;
    box-shadow: 0 0 2rem rgba(0, 0, 0, 0.17);
}


@media (max-width: 1000px) {
    .history_containt {
        flex-direction: column;
    }

    .history_containt .description {
        width: 100%;
    }

    .history_containt img {
        width: 100%;
    }
}

@media (max-width: 501px) {
    .history_containt img {
        min-width: 0rem;
    }
}

