#Service_details .Service_section_container {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

#Service_details .service_section_containt {
    width: 80%;
    display: flex;
    gap: 1rem;
}

#Service_details .service_section_containt .side_bar {
    display: flex;
    flex-direction: column;
    gap: .3rem;
    width: 25%;
    min-width: 16rem;
    /* border: 1px solid black; */
}

#Service_details .service_section_containt .side_bar h3 {
    font-size: 1.1rem;
    border: 1px solid black;
    color: white;
    background-color: rgb(33, 33, 33);
    padding: .8rem 1rem;
    margin: 0;
    transition: .25s;
    cursor: pointer;
}

#Service_details .service_section_containt .side_bar h3:hover {
    background-color: #f8a555;
    border: 1px solid #f8a555;
}


/* ////////////////////////////////////////////////// */


#Service_details .service_section_containt .service_containt {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 75%;
    /* border: 1px solid black; */
}

#Service_details .service_section_containt .image_box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.287);
}

#Service_details .service_section_containt .image_box img {
    width: 100% ;
    height: 25rem;
    padding: .61rem;
    object-fit: cover;
}

#Service_details .service_section_containt .service_containt h2 {
    font-size: clamp(1.3rem, 2vw , 3vw);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(43, 43, 43);
    margin-bottom: 0;
}

#Service_details .service_section_containt .service_containt p {
    font-size: clamp(1rem, 1.4vw, 4vw); 
    line-height: 1.78rem;
    color: rgb(47, 47, 47);
    color: rgb(60, 60, 60);
}

#Service_details .service_section_containt .adventage {
    display: flex;
    align-items: start;

    gap: 2rem;
    margin-top: 3rem;
}

#Service_details .service_section_containt .adventage h2 {
    margin-top: 0;
}

#Service_details .service_section_containt .adventage .details {
    width: 60%;
}

#Service_details .service_section_containt .adventage .image_2_box {
    width: 40%;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.299);
}

#Service_details .service_section_containt .adventage .image_2_box img {
    width: 96%;
    height: 15rem;
    padding: .4rem;
    object-fit: cover;

}

@media (max-width: 1250px) {
    #Service_details .service_section_containt {
        display: flex;
        flex-direction: column-reverse;
        /* flex-direction: column; */
    }

    #Service_details .service_section_containt .side_bar {
        width: 100%;
        margin-top: 5rem;
    }

    #Service_details .service_section_containt .service_containt {
        width: 100%;
    }
}

@media (max-width: 900px) {
    #Service_details .service_section_containt {
        transform: translateX(-.5rem);
        width: 90%;
    }

    #Service_details .service_section_containt p {
        font-size: 1.2rem !important;
    }


    #Service_details .service_section_containt .service_containt .adventage {
        flex-direction: column;
    }

    #Service_details .service_section_containt .service_containt .details {
        width: 100%;
    }

    #Service_details .service_section_containt .service_containt .image_2_box {
        width: 100%;
    }

    #Service_details .service_section_containt .image_box img {
        width: 100% ;
        height: 18rem;
        padding: .61rem;
        object-fit: cover;
    }

    #Service_details .service_section_containt .side_bar {
        min-width: 0;
    }
}