nav {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    /* border: 1px solid red; */
    width: 100vw;
    z-index: 99999999999;
}

.Link {
    text-decoration: none;
}

nav .navbar_container {
    width: 100%;
}

/* navbar_1 */
nav .navbar_1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5vw;
    /* border: 1px solid green; */
    color: white;
    background-color: #1C2755;
}

nav .navbar_1 i {
    font-size: 1.3rem;
    cursor: pointer;
}

nav .navbar_1 i:hover {
    color: #f8a555;
}

nav .navbar_1 .contact {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

nav .navbar_1 .contact .phone_email {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .51rem;
}

/* //////////////////////////// */


nav .navbar_1 .media {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.52rem;
}



/* //////////////////////////////////////////////////////////////// */
/* navbar_2 */
nav .navbar_2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5vw;
    box-shadow: 0rem 0rem 1rem #0000002f;
    background-color: white;
    height: 4rem;
}

nav .navbar_2 .logo_section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

nav .navbar_2 .logo_section img {
    width: 17rem;
    /* border: 1px solid red; */
    height: 5rem;
    object-fit: cover;
}

nav .navbar_2 .logo_section i {
    display: none;
}

nav .navbar_2 ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

nav .navbar_2 ul li {
    list-style-type: none;
}


nav .navbar_2 ul li a {
    text-decoration: none;
    font-weight: bold;
    color: black;
}

nav .navbar_2 ul a:hover {
    color: #f8a555;
    /* border-bottom: 1px solid #f8a555; */
}






/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */
/* ///////////////////////////////////////////////////////////////////////////////////////// */


nav .navbar_2 .Link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid black; */
    padding: 1.42rem 0;

}



nav .navbar_2 .Other_link {
    position: absolute;
    width: 10rem;
    background-color: #1C2755;
    top: 4rem;
    left: 0;
    display: none;

}

nav .navbar_2 .Link:hover .Other_link {
    display: block;
}

nav .navbar_2 .Other_link .Link {
    padding: .61rem 0;
    border-bottom: 1px solid rgba(44, 194, 142, 0.127);
}

nav .navbar_2 .Other_link .Link:hover a {
    color: #f8a555;
}

nav .navbar_2 .Other_link .Link a {
    color: white;
}


.bi-chevron-down {
    margin-left: .3rem;
    font-size: .8rem;
}

















@media(max-width: 900px) {
    nav .navbar_1 {
        display: none !important;
    }

   nav .navbar_2 {
        flex-direction: column;
        align-items: start;
        padding: 0;
        gap: 0;
    }

    nav .navbar_2 .logo_section {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    nav .navbar_2 ul {
        display: none;
        height: 0 ;
        transition: 1s;
        background-color: white;
        padding-bottom: 2rem !important;

    }

    nav .navbar_2 .logo_section i {
        display: block;
        margin-right: 5vw !important;
        color: black !important;
        font-size: 2rem !important;
        cursor: pointer;
    }

    nav .navbar_2 .logo_section img {
        width: 12rem;
        height: 3rem;
    }

    nav .navbar_2 .logo_section i:hover {
        color: #f8a555 !important;
    }

    nav.active .navbar_2 ul {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 0;
        gap: 0rem;
        height: auto;
    }

    nav .navbar_2 ul .Link {
        border-top: 1px solid rgba(128, 128, 128, 0.37);
        width: 100%;
        padding: 1rem 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    nav .navbar_2 ul .Link:hover {
        background-color: #f8a555;
        background-color: #f8a45548;
        cursor: pointer;
    }


    nav .navbar_2 ul li a:hover {
        border: none;
        color: black;
    }  


    /* ///////////////////////////////////////////////////////////////////////////// */
    

    nav .navbar_2 .Link {
        flex-direction: column;
        gap: .0rem;
        /* border: 1px solid red; */
        /* padding: 1rem 0 !important; */

    }

    nav .navbar_2 .Link.active {
        padding-bottom: 0 !important;
        gap: .7rem;
    }

    
    nav .navbar_2 .Other_link {
        position: relative;
        width: 100%;
        background-color: #1C2755;
        display: block;
        top: 0rem;
        left: 0;
        overflow: hidden;
    
    }

    nav .navbar_2 .Link.activ .Other_link {
        height: 0;
        border: none;
    }

    nav .navbar_2 .Other_link .Link {
        padding: .71rem 0;
    }



}


