#Blog1 .Blog_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin: 7rem 0;
}

#Blog1 .blog_containt {
    width: 90%;
}

#Blog1 .blog_containt h1 {
  margin-bottom: 3rem;
}

#Blog1 .blog_containt .blog_list {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 30%));
    gap: 2rem;
    row-gap: 5rem;
}

#Blog1 .blog_containt .blog_list .single_blog {
    box-shadow: 0rem 0rem 2rem rgba(67, 67, 67, 0.178);
    transition: .25s;
    background-color: white;
}

#Blog1 .blog_containt .single_blog:hover {
    scale: 1.051;
}

#Blog1 .blog_containt .single_blog .img_section {
    position: relative;
    width: 100%;
    height: 15rem;
    background-color: gray;
    
}

#Blog1 .blog_containt .single_blog .img_section p {
    position: absolute;
    font-weight: bold;
    bottom: -1rem;
    left: 1;
    padding: .6rem 1.5rem;
    border-top: 1px solid white;
    border-right: 1px solid white;
    background-color: #f8a555;
    color: white;
}

#Blog1 .blog_containt .single_blog .img_section img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

#Blog1 .blog_containt .single_blog .details {
    padding: 1rem;
}


#Blog1 .blog_containt .single_blog .details h3 {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin-top: 0;
  margin-bottom: .8rem;
  letter-spacing: .051rem;
  color: rgba(0, 0, 0, 0.795);
}

#Blog1 .blog_containt .single_blog .details p {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    color: rgba(0, 0, 0, 0.764);
    font-size: clamp(1rem, 1.15vw, 4vw);
    margin-top: .0rem;
    line-height: 1.4rem;
}

#Blog1 .blog_containt .single_blog .details button {
    border: none;
    cursor: pointer;
    color: white;
    padding: .6rem 1.4rem;
    transition: .25s;
    background-color: #f8a555;
}

#Blog1 .blog_containt .single_blog .details button:hover {
    padding: .6rem 2rem;
    border-radius: .5rem;
}

@media (max-width: 601px) {
    #Blog1 .blog_containt .blog_list {
        grid-template-columns: repeat(auto-fit, 80vw);
        gap: 2rem;
    }

    #Blog1 .Blog_container h1 {
        font-size: clamp(1.4rem, 5vw, 4rem);

    }
}

@media (max-width: 501px) {
    #Blog1 .blog_containt {
    width: 90%;
}
}