#Footer1 .Footer_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-top: 18rem;

}

#Footer1 .footer_top {
    display: flex;
    align-items: end;
    width: 100%;
    height: 17rem;
    background-color: #1e3079;
    color: white;

}

#Footer1 .footer_top .triangle {
    border-top: 18rem solid transparent;
    border-left: 18rem solid transparent;
    border-bottom: 0rem solid #f8a555;
    border-left: 18rem solid #f8a555;
}

#Footer1 .footer_top .right_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 17rem;
    min-width: 35rem;
}

#Footer1 .footer_top .right_section h3 {
    font-size: clamp(1.1rem, 2vw, 4vw);
    margin: .3rem;
    width: 90%;
}

#Footer1 .footer_top .right_section p {
    font-size: clamp(1rem, 1.2vw, 4vw);
    width: 85%;
    color: rgba(255, 255, 255, 0.83);
}

#Footer1 .footer_top .right_section .call {
    color: #f8a555;
}

#Footer1 .footer_top .img_box {
    position: relative;
    height: 18rem;
    width: 100%;
    width: 25vw;
    background-color: #f8a555;
}

#Footer1 .footer_top .img_box img {
    position: absolute;
    object-fit: cover;
    object-position: top;
    width: 22rem;
    height: 25rem;
    right: -10rem;
    bottom: 0;
}

/* /////////////////////////////////////////////////////////////////////// */

#Footer1 .Footer_container .footer_2 {
    display: grid;
    justify-content:center;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 22%));
    gap: 2rem;
    width: 100%;
    padding-top: 4rem;
    padding-bottom: 3rem;
    background-color: #1C2755;
    color: white;

}

#Footer1 .Footer_container .footer_2 h3 {
    margin-top: 0;
    color: #f8a555;
}


#Footer1 .Footer_container .footer_2 .part_1 img {
    object-fit: cover;
    width: 15rem;
    height: 4rem;
    background-color: white;
}

#Footer1 .footer_2 .part_1 p {
    line-height: 1.6rem;
    width: 90%;
    font-size: clamp(1rem, 1.1vw, 4vw) !important;

}

#Footer1 .footer_2 .link {
    display: flex;
    gap: 1.5rem;
    font-size: 1.4rem;
    padding: 1rem 0;
    color: #f8a555;
    
}

#Footer1 .footer_2 i {
    cursor: pointer;
}

#Footer1 .footer_2 .more {
    cursor: pointer;
    color: #f8a555;
}

#Footer1 .footer_2 .part_2 p:hover,
#Footer1 .footer_2 .part_3 p:hover{
    cursor: pointer;
    color: #f8a555;
}

#Footer1 .Footer_container .footer_2 p {
    font-size: 1.2rem;
}

#Footer1 .footer_2 .part_4 .info_box {
    display: flex;
    gap: 1rem;
}

#Footer1 .footer_2 .part_4 p {
    width: 90%;
    margin-top: 0;
}

#Footer1 .footer_2 .part_4 i {
    transform: translateY(.3rem);
    color: #f8a555;
}


































@media (max-width: 1160px) {
    #Footer1 .footer_top .img_box img {
        right: -20vw;
    } 
}

@media (max-width: 950px) {
    #Footer1 .footer_top .img_box img {
        right: -30vw;
    } 
}


@media (max-width: 900px) {
    #Footer1 .footer_top .triangle {
        border-top: 17rem solid transparent;
        border-left: 17rem solid transparent;
        border-bottom: 0rem solid #f8a555;
        border-left: 17rem solid #f8a555;
    }

    #Footer1 .footer_top .img_box {
        height: 17rem;

    }
}

@media (max-width: 860px) {
    #Footer1 .footer_top .right_section {
        min-width: 0rem;
    }


    #Footer1 .footer_top .img_box {
        display: none;
    } 

    #Footer1 .footer_top .triangle {
        display: none;
    }

    #Footer1 .footer_top {
        display: flex;
        justify-content: center;
        align-items: center;
        height: auto !important;
    }

    #Footer1 .footer_top .right_section {
        height: auto;
        width: 90%;
        min-width: 0rem;
        padding: 1.5rem 0;
    }
}

@media (max-width: 501px) {
    #Footer1 .Footer_container {
        background-color: #1C2755;
        margin-top: 8rem;
    }


    #Footer1 .Footer_container .footer_2 {
        display: grid;
        grid-template-columns: 1fr;
        width: 80%;
    }

}