 .Blog_page_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8rem 0;
}

 .Blog_Page_Box {
    display: flex;
    flex-direction: column;
    gap: 5rem;
}

 .blog_page_containt {
    display: flex;
    gap: 2rem;
    width: 100%;
    padding: 0 8vw;

    
}

 .blog_page_containt .blog_list {
    display: flex;
    flex-direction: column;
    gap: 5rem;

}

 .blog_page_containt .blog_list .single_blog {
    width: 100%;
    background-color: rgba(128, 128, 128, 0.1);
    padding-bottom: 2rem;
}

 .blog_page_containt .single_blog .image_box {
    position: relative;
    width: 100%;
    height: 20rem;
    overflow: hidden;
}

 .blog_page_containt .single_blog .image_box img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* object-position: top; */
    transition: .7s;
}

 .blog_page_containt .single_blog .image_box img:hover {
    scale: 1.1;
    cursor: pointer;
    
}

 .blog_page_containt .single_blog .date {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 0;
    bottom: 0rem;
    background-color: #f8a555;
    color: white;
    width: 6rem;
}

 .blog_page_containt .single_blog .date h2 {
    margin: 0;
    margin-top: .1rem;
    font-size: 2rem;
    /* font-size: clamp(1rem, 5vw , 5vw); */
}

 .blog_page_containt .single_blog .date p {
    margin: 0;
    margin-bottom: .5rem;

}


 .blog_page_containt h3 {
    padding: 0 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: clamp(1rem , 1.7vw, 4vw);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgba(0, 0, 0, 0.834);
}

 .blog_page_containt .single_blog .post_details {
    display: flex;
    gap: 2rem;
    padding: 0 1rem;
    border-top: 1px solid rgba(128, 128, 128, 0.326);
}

 .blog_page_containt .single_blog .post_details div {
    display: flex;
    align-items: center;
    gap: .5rem;
    color: rgb(84, 84, 84);
}

 .blog_page_containt .single_blog .post_details div i {
    color: #f8a555;
}


 .blog_page_containt .single_blog .text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    font-size: clamp(1rem, 1.16vw, 4vw);
    color: rgb(67, 67, 67);
    padding: 0 1rem;
    line-height: 1.8rem;
    margin-top: 0;
}

 .blog_page_containt .single_blog button {
    width: 9rem;
    margin-left: 1rem;
    padding: .5rem 0;
    font-size: 1.1rem;
    background-color: #f8a555;
    color: rgba(255, 255, 255, 0.955);
    border: none;
    font-weight: bold;
    cursor: pointer;
    transition: .25s;
}

 .blog_page_containt .single_blog button:hover {
    width: 10rem;
    border-radius: .5rem;
}


 .blog_page_containt .all_section {
    display: flex;
    gap: .8rem;
    align-items: center;
}

 .blog_page_containt .all_section div {
    border: 1px solid rgba(128, 128, 128, 0.446);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
    cursor: pointer;
}

 .blog_page_containt .all_section div.active {
    background-color: #f8a555;
    color: white;
    border: 1px solid #f8a555;
}

/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */
/* //////////////////////////////////////////////////////////////////////////////////// */

 .blog_page_containt .recent_post {
    margin-bottom: 1rem;
    margin-top: 6rem !important;
}



 .blog_page_containt .Right_section {
    width: 30%;
    min-width: 15rem;
    border-left: 1px solid rgba(128, 128, 128, 0.344);
    padding: 0 1rem;
}

 .blog_page_containt .Right_section .search {
    width: 100%;
    border: 1px solid rgba(77, 77, 77, 0.609);
    display: flex;
    align-items: center;
}

 .blog_page_containt .Right_section .search input {
    width: 80%;
    height: 3rem;
    font-size: 1.1rem;
    padding-left: .5rem;
    border: none;
    border-right: 1px solid rgba(128, 128, 128, 0.712);
}

 .blog_page_containt .Right_section .search input:focus {
    outline: none;
}

 .blog_page_containt .Right_section .search i {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    background-color: #f8a555;
    color: white;
    height: 3.1rem;
    cursor: pointer;
}


 .blog_page_containt .Right_section h2 {
    margin-top: 4rem;
    border-bottom: 2px solid black;
    padding-bottom: .5rem;
}

 .blog_page_containt .Right_section .single_categore {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid rgba(128, 128, 128, 0.302);
    cursor: pointer;
    padding-left: .5rem;
    
}

 .blog_page_containt .Right_section .single_categore:hover {
    background-color: rgba(128, 128, 128, 0.228);
}

 .blog_page_containt .Right_section .single_categore p {
    font-weight: bold;
    font-size: 1.152rem;
    margin: .4rem 0;
    padding: .5rem 0;
}


 .blog_page_containt .Right_section .single_recent_post {
    border-bottom: 1px solid gray;
    padding: .1rem 1rem;
    font-size: 1.05rem;
    font-weight: bold;
    color: rgb(48, 48, 48);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    transition: .25s;
}

 .blog_page_containt .Right_section .single_recent_post:hover {
    background-color: rgba(128, 128, 128, 0.158);
    cursor: pointer;
}

 .blog_page_containt .Right_section .single_recent_post .title {
    font-size: clamp(.1rem, 1vw, 4vw);
}

 .blog_page_containt .Right_section .single_recent_post .date {
    color: #f8a555;
}

 .blog_page_containt .Right_section .single_recent_post i {
    margin-right: .8rem;
    font-size: 1rem;
}

 .Link {
    text-decoration: none;
}


@media (max-width: 1100px) {
     .blog_page_containt {
        flex-direction: column;
        /* transform: translateX(-.5rem); */
    }

     .blog_page_containt .blog_list {
        width: 100% !important;
    }

     .blog_page_containt .Right_section {
        width: 100%;
        border: none;
        margin-top: 5rem;
        /* border: 1px solid black; */
        padding: 0;
    }

     .blog_page_containt .single_blog .post_details {
        gap: 2rem !important;
        /* border: 1px solid red; */
        padding-top: 1rem !important;
    }



     .blog_page_containt .single_blog .post_details p {
        font-size: clamp(.8rem, 1.5vw , 1.2rem) ;
        margin: 0;
    }

     .blog_page_containt .Right_section h2 {
        font-size: clamp(1.3rem, 2.5vw , 2.5vw);
    }

     .blog_page_containt .single_blog .post_details {
        margin-bottom: .51rem;
    }
}


@media (max-width: 501px) {
     .blog_page_containt {
        padding: 0 3vw;        
    }
    


     .blog_page_containt .single_blog .post_details {
        gap: 1rem !important;
    }

     .blog_page_containt .single_blog .post_details div {
        flex-direction: column;
        gap: 0;
    }

     .blog_page_containt .single_blog .image_box {
        height: 15rem;
    }

}