#Service2 .Process_container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding-top: 5rem;
    padding-bottom: 8rem;
    margin-bottom: 5rem;
    background-color: rgba(128, 128, 128, 0.124);
}

#Service2 .process_containt {
    width: 80%;
}

#Service2 .process_containt h2 {
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    letter-spacing: .1rem;
    font-size: 2rem;
    color: rgba(0, 0, 0, 0.843);
}

#Service2 .process_containt .process {
    display: flex;
    align-items: center;
    width: 100%;
    padding-top: 4rem;
}

#Service2 .process_containt .process ._3_process {
     display: flex;
     align-items: center;
     width: 75%;
}

#Service2 .process_containt .process ._3_process .single_process {
    width: 33.33%;
}

#Service2 .process_containt .process ._1_process {
    width: 25%;
}

#Service2 .process_containt .process .single_process {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


#Service2 .process_containt .process .single_process p {
    white-space: nowrap;
    font-weight: 600;
    font-size: 1.2rem;
    margin-top: 1rem;
}

#Service2 .process_containt .process .single_process .icon_box {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 8rem;
    height: 8rem;
    border-radius: 10rem;
    border: 2px solid #f8a555;
}

#Service2 .process_containt .single_process .icon_box i {
    font-size: 3rem;
}

#Service2 .process_containt .single_process .icon_box .bi-chevron-double-right {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    cursor: pointer;
    font-size: 1rem;
    right: -1.4rem;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 10rem;
    border: 1px solid #f8a555;
    background-color: #f8a555;
    color: white;
    
}

#Service2 .process_containt .bi-arrow-right {
    font-size: 2rem;
    transform: translateY(-1.9rem);
}

@media (max-width: 1140px) {
    #Service2 .process_containt {
        width: 95%;
    }
}


@media (max-width: 970px) {

    #Service2 .process_containt {
        width: 100%;
    }

    #Service2 .process_containt ._3_process {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100% !important;
    }


    #Service2 .process_containt ._3_process .single_process {
        width: 25% !important;
        min-width: 13rem;
    }


    #Service2 .process_containt .process {
       flex-direction: column;
       gap: 3rem;
    }

    #Service2 .bi-arrow-right.third {
        display: none;
    }
}


@media (max-width: 800px) {

    #Service2 .process_containt .process {
        gap:  2rem;
    }


    #Service2 .process_containt ._3_process {
        flex-direction: column;
        gap: 2rem;
    }

    #Service2 .bi-arrow-right {
        display: none;
    }

    #Service2 .process_containt h2 {
        text-align: center;
        font-size: clamp(1.4rem, 4vw, 3rem);
        margin: 0 1.5rem;
    }
}

@media (max-width: 501px) {
    #Service2 .Process_container {
        transform:  translateX(-.5rem);
    }
}
