#Faq1 .Faq_containter {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    padding: 5rem 0;
}

#Faq1 .Faq_containter h2{
    width: 80%;
    font-size: 2.2rem;
    margin-bottom: 4rem;
}

#Faq1 .faq_containt {
    display: flex;
    gap: 2rem;
    justify-content: center;
    width: 80%;
    margin-bottom: 7rem;

}

#Faq1 .faq_containt img {
    object-fit: cover;
    width: 45%;
    height: 22rem;
}

#Faq1 .faq_containt .questions_list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 55%;
}

#Faq1 .faq_containt .single_question {
    overflow: hidden;
    width: 100%;
    transition: .25s;
    border: 1px solid rgba(128, 128, 128, 0.539);
    background-color: white;
}

#Faq1 .faq_containt .single_question.active .answer {
    position: relative;
    height: auto;
    height: 7rem;
}

#Faq1 .faq_containt .single_question .answer.active {
    height: 14rem;
}

#Faq1 .faq_containt .single_question.active .question {
    border-bottom: 1px solid rgba(128, 128, 128, 0.539);

}

#Faq1 .faq_containt .questions_list button {
    border: none;
    cursor: pointer;
    width: 10rem;
    border-radius: .2rem;
    padding: .4rem 0;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    font-weight: bold;
    transition: .25s;
    background-color: #f8a555;
    border: 1px solid #f8a555;
}

#Faq1 .faq_containt .questions_list button:hover {
    width: 12rem;
}


#Faq1 .faq_containt .question {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: .71rem;
    padding: 0 .6rem;
    padding-right: 2rem;
    background-color: rgba(128, 128, 128, 0.242);
}


#Faq1 .faq_containt .question i {
    cursor: pointer;
    font-size: 1.52rem;
}

#Faq1 .faq_containt .question i:hover {
   color: #f8a555;

}

#Faq1 .faq_containt h3 {
    font-size: 1.2rem;
    padding: .6rem 0rem;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(40, 40, 40);
}


#Faq1 .faq_containt p {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    font-size: 1.2rem;
    color: rgb(48, 48, 48);
    margin: 1rem .6rem;
    line-height: 1.72rem;
}

#Faq1 .faq_containt .answer.active p {
    -webkit-line-clamp: 200; 
   
}

#Faq1 .faq_containt .answer {
    display: flex;
    overflow: hidden;
    height: 0;
    transition: .5s;

}

#Faq1 .faq_containt .answer p span {
    cursor: pointer;
    text-decoration: underline;
    color: rgba(0, 106, 255, 0.792);
}

#Faq1 .faq_containt .single_question .Read_more {
    position: absolute;
    text-decoration: underline;
    cursor: pointer;
    bottom: 1.1rem;
    right: .51rem;
    color: rgba(0, 106, 255, 0.792);
    background-color: white;
    margin: 0;
}


@media  (max-width: 1100px) {
    #Faq1 .Faq_containter h2 {
        width: 90%;
    }


    #Faq1 .faq_containt {
        width: 90%; 
    }
}

@media (max-width: 1000px) {
    #Faq1 .faq_containt img {
        display: none;
    }

    #Faq1 .Faq_containter {
        padding: 0 !important;
        margin: 0;
        width: 100vw;
    }

    #Faq1 .faq_containt .questions_list {
        width: 100%;
    }

    #Faq1 .Faq_containter h2 {
        margin-bottom: 3rem;
        font-size: clamp(1rem, 7vw, 2.5rem) !important;
    }
}




@media (max-width: 501px) {
    #Faq1 .faq_containt .single_question .answer.active {
        height: auto;
        height: 20rem;
        height: 100%;
    }
}



