#Hero_2  {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 100vw;
    padding-top: 9rem;
    height: 80vh;
    z-index: 8;

}

#hero_2_container2 {
    width: 80%;
}


#Hero_2 .image_box {
    position: absolute;
    inset: 0;
    z-index: -1;
    transition: .25s;
}

#Hero_2_img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: brightness(30%);
}



#Hero_2 .hero_containt {
    overflow: hidden;
    width: 40rem;
    z-index: 9;
}

#Hero_2 .hero_containt h1 {
    font-size: 3.4rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.672rem;
    margin-top: .4rem;
    opacity: 0;
    transform: translateY(-6rem);
    color: white;
}

#Hero_2 .hero_containt .title2 {
   color: #f8a555;
}

#Hero_2 .hero_containt p {
    opacity: 0;
    font-size: 1.2rem;
    line-height: 1.62rem;
    margin: 2rem 0;
    padding-top: 1.52rem;
    transform: translateY(6rem);
    color: white;

}

#Hero_2 .hero_containt .button_section {
    display: flex; 
    opacity: 0;
    gap: 1rem;
    transform: translateY(6rem);

}

#Hero_2 .hero_containt .button_section button {
    border: none;
    cursor: pointer;
    padding: .7rem 2rem;
    border-radius: .2rem;

}

#Hero_2 .hero_containt .button_section .service {
    color: white;
    font-weight: bold;
    background-color: #1C2755 !important;
}

#Hero_2 .hero_containt .button_section .more {
    font-weight: bold;
    background-color: #f8a555;
}


#Hero_2  .prev,
#Hero_2  .next {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    width: 4vw;
    height: 4vw;
    bottom: 2rem;
    max-width: 4rem;
    min-width: 3rem;
    max-height: 4rem;
    min-height: 3rem;
    border: 3px solid rgba(255, 255, 255, 0.176);
    border-radius: 10rem;
    z-index: 2;
    background-color: transparent;
    color: white;
}

#Hero_2  i {
    font-size: 1.8rem;
    color: rgba(255, 255, 255, 0.404);
}

#Hero_2  .prev {
    left: 4.2rem;
}

#Hero_2  .next {
    right: 5.2rem  ;
}






@media (max-width: 900px) {
    #Hero_2  {
        padding-top: 5rem;
    }
}







@media (max-width: 800px) {
    #Hero_2 .hero_containt h1 {
        font-size: clamp(1.8rem, 6.5vw, 6rem);
    }

    #Hero_2 .hero_containt p {
        font-size: clamp(1.2rem, 2.3vw, 6rem);

    }

    #hero_2_container2 {
        width: 90%;
    }

    #Hero_2 .hero_containt {
        width: 85vw;
    }

}

@media (max-width: 501px) {
    #Hero_2 .hero_containt .button_section {
        justify-content: center;
        width: 85vw;
    }

    #Hero_2 .hero_containt .button_section button {
        width: 50%;
        padding-left: 0;
        padding-right: 0;
    }

    #Hero_2 .hero_containt h1 {
        font-size: 9vw;
    }


}





#Hero_2.active .hero_containt h1 {
    transform: translateY(-0rem);
    transition: 1s !important;
    opacity: 1;



}

#Hero_2.active .hero_containt p,
#Hero_2.active .hero_containt .button_section {
   transform: translateY(-0rem);
   transition: 1s !important;
   opacity: 1;


}



