#Service1 .Service_container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10rem;
    margin-top: 13rem;
}

#Service1 .service_containt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

#Service1 .service_containt .title_section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 82%;
    margin-bottom: 1.5rem;
}

#Service1 .service_containt .title_section .icons {
    display: flex;
    gap: 1.5rem;
}


#Service1 .service_containt .title_section .icons i {
    padding: .5rem .5rem;
    font-size: 1.2rem;
    border: 1px solid gray;
}

/* Slider container */
#Service1 .service_containt .slider_container {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    width: 80vw;
    border: none;
}

#Service1 .service_containt .slider_containt {
    display: flex;
    align-items: center;
    gap: 1.67vw;
    margin-bottom: 2rem;
    transition: .5s;
    width: 100%;
    padding-top: 2rem;

    
    
}

/* 80vw   25.9vw 27.6vw */

#Service1 .service_containt  .slider_containt.scroll1 {
    transition: 0.5s;
}

#Service1 .service_containt  .slider_containt.scroll2 {
    transition: 0s;
}

#Service1 .service_containt .slider_container .slider_1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    min-width: 25.9vw !important;
    transition: .5s;
    padding-bottom: 1rem;
    background-color: white;
    box-shadow: 0 0 1rem rgba(128, 128, 128, 0.516);

}

#Service1 .service_containt .slider_1 .slider_1_box {
    width: 98%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


#Service1 .service_containt .slider_container .slider_1 img {
    object-fit: cover;
    width: 105%;
    height: 14rem;
}

.service_containt .slider_1:hover {
    scale: 1.05;
}

#Service1 .service_containt .slider_1 h3{
    display: flex;
    align-items: center;
   margin: 0;
   width: 95.6%;
   width: 100%;
   height: 3rem;
   padding-left: 3rem;
   background-color: #1C2755;
   color: white;
}

#Service1 .service_containt .slider_1 p {
    display: -webkit-box;
    -webkit-line-clamp: 4; 
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 1rem;
    padding-left: 1.3rem;
    padding-right: 2rem;
    color: rgb(89, 89, 89);
}

#Service1 .service_containt .slider_1 .btn {
     width: 100%;
}

#Service1 .service_containt .slider_1 button {
    font-weight: bold;
    cursor: pointer;
    border: none;
    padding: .6rem 1rem;
    font-size: .98rem;
    margin-left: 1.5rem;
    transition: .25s;
    border-radius: .4rem;
    color: white;
    background-color: #f8a555;
}

#Service1 .service_containt .slider_1 button:hover {
    padding: .6rem 2rem;
    border-radius: .51rem;
}




/* style={{ transform: `translateX(${count * -35}%)` }} */



@media (max-width: 1000px) {
    #Service1 .service_containt .slider_container {
        width: 90vw !important;
    }

    #Service1 .service_containt .slider_container .slider_1 {
        min-width: 44vw !important;
    }
}

@media (max-width: 600px) {
    #Service1 .service_containt .slider_container {
        width: 90vw !important;
    }

    #Service1 .service_containt .slider_container .slider_1 {
        min-width: 90vw !important;
    }

    #Service1 .service_containt .title_section {
        margin-bottom: 0rem;
    }

    #Service1 .service_containt .title_section h1 {
        font-size: clamp(1rem, 5vw, 2rem);
    }
}