.Service_section_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}



.Service_section_containt {
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(22rem, 20vw));
    justify-content: center;
    gap: 2rem;
    row-gap: 5rem;
}


.services_header_page {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
}

.service_header_containt {
    width: 80%;
}


.service_header_containt h2 {
    font-size: 2rem;
}

.service_header_containt p {
    font-size: 1.18rem;
    line-height: 1.8rem;
    color: rgb(69, 69, 69);
}


.Service_section_containt .slider_1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: .5s;
    box-shadow: 0 0 1rem rgba(128, 128, 128, 0.516);
    padding-bottom: 1rem;
    overflow: hidden;
    background-color: white;
    text-decoration: none;

}

.Service_section_containt .slider_1 .slider_1_box {
    width: 98%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* padding: 1rem; */
    /* border: 1px solid red; */
}


.Service_section_containt .slider_1 img {
    width: 105%;
    height: 14rem;
    object-fit: cover;
}

.Service_section_containt .slider_1:hover {
    scale: 1.05;
    /* z-index: 999999999; */
}

.Service_section_containt .slider_1 h3{
   /* border: 1px solid red; */
   margin: 0;
   width: 95.6%;
   width: 100%;
   background-color: #1C2755;
   color: white;
   height: 3rem;
   display: flex;
   align-items: center;
   /* transform: translateX(4rem); */
   padding-left: 2rem;
}

.Service_section_containt .slider_1 p {
    font-size: 1.15rem;
    color: rgb(60, 60, 60);
    padding-left: 1rem;
    padding-right: 2rem;
    line-height: 1.6rem;
    /* border: 1px solid red; */
    /* width: 80%; */
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
}

.Service_section_containt .slider_1 .btn {
     width: 100%;
}

.Service_section_containt .slider_1 button {
    padding: .6rem 1rem;
    font-size: .98rem;
    border: none;
    background-color: #f8a555;
    color: white;
    font-weight: bold;
    margin-left: 1rem;
    cursor: pointer;
    transition: .25s;
    border-radius: .3rem;
}

.Service_section_containt .slider_1 button:hover {
    padding: .6rem 2rem;
    border-radius: .51rem;
}

@media (max-width: 501px) {
    .service_header_containt {
        width: 90%;
    }

    .service_header_containt h2 {
        font-size: clamp(1.2rem, 5vw, 2rem);
    }

    .Service_section_containt {
        grid-template-columns: repeat(auto-fill, 90vw);

    }
}

