#Testimonial2 .Testimonial_page_container {
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

#Testimonial2 .testimonial_page_containt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
}

#Testimonial2 .testimonial_page_containt .title_section {
    text-align: center;
    width: 80%;
}

#Testimonial2 .testimonial_page_containt .title_section h2 {
    font-size: clamp(1.4rem , 2vw, 4vw);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(53, 53, 53);
}

#Testimonial2 .testimonial_page_containt .title_section .desc {
    font-size: clamp(1rem, 1.18vw, 4vw);
    line-height: 1.7rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(65, 65, 65);

}

#Testimonial2 .testimonial_page_containt .testimonial_list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(20rem, 28%));
    justify-content: center;
    gap: 2rem;
    row-gap: 4rem;
    margin-top: 4rem;
} 

#Testimonial2 .testimonial_page_containt .testimonial_list .single_testimonial {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1.52rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.188);
    border-radius: .4rem;
    background-color: white;
}


#Testimonial2 .testimonial_page_containt .single_testimonial .bi-quote {
    position: absolute;
    font-size: 2.4rem;
    color: #f8a555;
    top: .351rem;
    left: .351rem;
}



#Testimonial2 .testimonial_page_containt .testimonial_list .single_testimonial img {
     width: 4rem;
     height: 4rem;
     border-radius: 10rem;
     object-fit: cover;
}

#Testimonial2 .testimonial_page_containt .single_testimonial p {
    font-size: 1rem;
    line-height: 1.4rem;
    color: rgb(98, 98, 98);
}

#Testimonial2 .testimonial_page_containt .single_testimonial h3 {
    font-size: 1.1rem;
    margin-bottom: 0;
}

#Testimonial2 .testimonial_page_containt .single_testimonial .job {
    margin-top: 0.3rem;
    font-size: .9rem;
    color: #f8a555;
}


@media (max-width: 700px) {
    #Testimonial2 .testimonial_page_containt .title_section {
        width: 95%;
    }
}

@media (max-width: 501px) {

    #Testimonial2 .testimonial_page_containt .title_section .desc {
        line-height: 1.5rem;
    
    }

    #Testimonial2 .testimonial_page_containt .testimonial_list {
        grid-template-columns: repeat(auto-fill, minmax(0rem, 90%));
    } 

    #Testimonial2 .testimonial_page_containt .single_testimonial {
        padding: 1.5rem .5rem !important;
    }
}