.About_page_container {
    margin: 6rem 0;
    width: 100vw;
}

.About_page_container .image_box {
    position: relative;
    display: flex;
    align-items: center;
    /* padding-left: 5vw; */
    height: 20rem;
    background-color: rgba(0, 0, 0, 0.689);
}

.About_page_container .image_box img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    z-index: -1;
}

.About_page_container .image_box h2 {
    font-size: 2rem;
    padding-left: 10vw;
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


.About_page_container .bottom_section {
    background-color: rgba(128, 128, 128, 0.472);
    background-color: rgba(128, 128, 128, 0.372);
}

.About_page_container .bottom_section p {
    font-size: 1.2rem;
    margin: 0;
    padding: 1.6rem 0 1.6rem 10vw;
    font-weight: 600;
}

.About_page_container .bottom_section p i {
    /* color: red; */
    margin: 0 .5rem;
    font-size: 1.2rem;
}

@media (max-width: 501px) {
    .About_page_container .image_box h2 {
        text-align: center;
        width: 80%;
    }
}